// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import './assets/sass/reset.css'
import './assets/sass/main.scss'
import axios from 'axios'
import promise from 'es6-promise'
import * as filters from './filters/' //
import store from './store/'
import ElementUI from 'element-ui'  //vue template
import 'element-ui/lib/theme-chalk/index.css'
import './assets/sass/element-variables.scss'
//ueditor
import '../static/UE/ueditor.config.js'
import '../static/UE/ueditor.all.min.js'
import '../static/UE/lang/zh-cn/zh-cn.js'
import '../static/UE/ueditor.parse.min.js'

Vue.use(ElementUI)

promise.polyfill() //promise ie9,10

//
var env = process.env.NODE_ENV
const api = {}
if(env=='development'){
  api.serverUrl = 'http://120.78.151.240:3722'
}
if(env=='production'){
  api.serverUrl = 'http://120.78.151.240:3722'
}

//nav
import navHmtl from '@/components/nav'
Vue.component('navHmtl',navHmtl)


// 
Object.keys(filters).forEach((key) => {
  Vue.filter(key, filters[key])
})

/*
 *  ajax  axios  https://www.kancloud.cn/yunye/axios/234845
 *  params url = string
 *  params opt = {}
 *  return promise
 */

Vue.prototype.$ajax = function(url,opt){
  return axios(Object.assign({
    method: 'POST',
    url:  api.serverUrl + url
  },opt))
}

Vue.prototype.axios = axios

//
//  ajax end

//
router.beforeEach(function(to,from,next){
  console.log(to)
  if(to.meta.title){
    document.title = to.meta.title;
  }
  if(to.meta.auth == true){
    //
    if(!localStorage.token){
      next('/login?redirect='+to.fullPath)
    }else{
      next()
    }
  }else{
    next()
  }
})

// 
Vue.prototype.$goRoute = function (index) {
  this.$router.push(index)
}

Vue.config.productionTip = false

var app = new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>',
  created () {
    console.log('created')
  }
})



