<template>
<div>
  <el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect">
    <el-menu-item index="1" v-on:click="$goRoute('/')"></el-menu-item>
    <el-menu-item index="2" v-on:click="$goRoute('/list/111222')"></el-menu-item>
    <el-menu-item index="3"><a href=""></a></el-menu-item>
  </el-menu>
</div>
</template>

<script>
export default {
  data () {
    return {
      activeIndex: '1',
      nav: [
        {
          text: '',
          link: '/'
        },
        {
          text: '',
          link: '/list/11111'
        },
        {
          text: '',
          link: '/detail'
        }
      ]
    }
  },
  methods: {
    handleSelect(key, keyPath) {
      console.log(key, keyPath);
    }
  }
}
</script>

<style>

</style>



